* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    list-style: none;
    text-decoration: none;
}

#img {
    width: 100px !important;
    height: 60px;
    position: absolute;
    top: 15px;
}

header {
    width: 100%;
    margin-bottom: 50px;
}

.dis {
    padding: 10px;
    display: flex;
}

nav {
    padding: 20px 0;
    display: flex;
    width: 80%;
    margin: auto;
    height: 40px;
    justify-content: space-between;
}

p {
    color: darkcyan;
}

ul {
    display: flex;
}

li {
    margin-left: 30px;
}

header a {
    color: #fff;
    margin-left: 30px;
}

header i {
    width: 40px;
    height: 40px;
    padding: 10px;
    text-align: center;
    border-radius: 50%;
    border: 2px solid #fff;
    margin: 0 10px;
}

header button {

    display: block;
    width: 50px;
    padding: 5px;
    border: none;
    color: white;
    background: #025b69;
    outline: none;
    border-radius: 3px;
}

header button:hover {
    background: white;
    color: darkcyan;
    transition: 0.5s;
    border: 1px solid darkcyan;
}

.cart {
    margin-top: 10px;
    color: darkcyan;
}

.toggle {
    display: none;
    width: 30px;
    height: 30px;
    border: none;
}

.close {
    display: none;
    color: white;
}

@media only screen and (max-width: 768px) {
    header nav .flexSB {
        display: none;

    }

    .close {
        display: block;
        position: absolute;
        top: -10%;
        right: 0;
    }

    .account, .cart {
        display: none;
    }

    .navMenu-list {
        position: fixed;
        display: flex;
        flex-direction: column;
        top: 0;
        left: 0;
        background-color: #025b69;
        width: 50%;
        height: 100vh;
        z-index: 9999;
    }

    p {
        color: white;
    }

    header ul li {
        align-items: center;
        margin-top: 90px;
        padding: 0 50px;
    }

    .toggle {
        display: block;
        position: absolute;
        top: 5%;
        right: 20px;
        border: none;
        font-size: 25px;
        margin: auto;
        cursor: pointer;
        background: none;
        color: darkcyan;
        transition: 0.5s;
    }

    .main {
        width: 90%;
        margin: auto;
    }
}


